import React, { Component } from "react";
import { Grid, Cell, List, ListItemContent, ListItem } from "react-mdl";

class Contacts extends Component {
  render() {
    return (
      <div className="contact-grid">
        <h2>Contact me :)</h2>
        <hr />
        <div className="contact-list">
          <List>
            <ListItem>
              <ListItemContent
                style={{
                  fontSize: "30px",
                  fontFamily: "Anton",
                  color: "white"
                }}
              >
                <i className="fa fa-phone-square" aria-hidden="true" />
                (+65) 9232 8328
              </ListItemContent>
            </ListItem>
            <ListItem>
              <ListItemContent
                style={{
                  fontSize: "30px",
                  fontFamily: "Anton",
                  color: "white"
                }}
              >
                <i className="fa fa-envelope" aria-hidden="true" />
                minghonglim@gmail.com
              </ListItemContent>
            </ListItem>
            <ListItem>
              <ListItemContent
                style={{
                  fontSize: "30px",
                  fontFamily: "Anton",
                  color: "white"
                }}
              >
                <i className="fa fa-skype" aria-hidden="true" />
                minghonglim_1
              </ListItemContent>
            </ListItem>
          </List>
        </div>
      </div>
    );
  }
}

export default Contacts;
