import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";

import AboutMe from "./aboutme";
import Resume from "./resume";
import Projects from "./projects";
import Contact from "./contact";

class LandingPage extends Component {
  render() {
    return (
      <div style={{ width: "100%" }}>
        <Grid className="landing-body">
          <Cell col={12}>
            <img src="images/avatar.png" alt="avatar" className="avatar-img" />
          </Cell>
          <div className="banner-text">
            <h1>Penultimate Student at National University of Singapore</h1>
            <hr />
            <p>My Skillset</p>
            <div className="social-links">
              {/* Linkedin */}
              <a
                href="https://www.linkedin.com/in/limminghong/"
                rel="noopener noreferrer"
                target="_blank"
              >
                <i className="fa fa-linkedin-square" aria-hidden="true"></i>
              </a>

              {/* Github */}
              <a
                href="https://github.com/Limminghong"
                rel="noopener noreferrer"
                target="_blank"
              >
                <i className="fa fa-github-square" aria-hidden="true"></i>
              </a>
            </div>
          </div>
          <AboutMe />
          <Resume />
          <Projects />
          <Contact />
        </Grid>
      </div>
    );
  }
}

export default LandingPage;
